@font-face {
  font-family: "Rajdhani-Bold";
  src: url("../fonts/Rajdhani-Bold.woff") format("woff2"), url("../fonts/Rajdhani-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Rajdhani-Medium";
  src: url("../fonts/Rajdhani-Medium.woff") format("woff2"), url("../fonts/Rajdhani-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Rajdhani-Regular";
  src: url("../fonts/Rajdhani-Regular.woff") format("woff2"), url("../fonts/Rajdhani-Regular.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

p {
  margin: 0;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page-body {
  margin: 0 auto;
  overflow-x: hidden;
  font-family: "Rajdhani", sans-serif;
  font-size: 16;
  color: #8A94A6;
}

@media (min-width: 1360px) {
  .wrapper {
    width: 1360px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
}

.container {
  width: 288px;
  margin: 0 auto;
}
@media (min-width: 720px) {
  .container {
    width: 688px;
  }
}
@media (min-width: 1360px) {
  .container {
    width: 616px;
    margin: 0 49px;
  }
}

img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: contain;
  object-position: center;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

.small-title {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  font-family: "Rajdhani-Bold";
}

.title {
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  font-family: "Rajdhani-Medium";
  color: #192B3F;
}

.choice {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 16px 0 20px 0;
}
@media (min-width: 720px) {
  .choice {
    grid-template-columns: 220px 1fr 1fr;
    padding: 24px 0 17px 0;
    row-gap: 38px;
  }
}
@media (min-width: 1360px) {
  .choice {
    grid-template-columns: 160px 198px auto;
    padding: 16px 0 20px 0;
    row-gap: 30px;
  }
}
.choice .choice__color {
  grid-column: span 2;
}
@media (min-width: 720px) {
  .choice .choice__color {
    grid-column: span 1;
  }
}
.choice .choice__color .choice__color-text {
  padding-bottom: 12px;
}
.choice .choice__color .choice__color-input {
  appearance: none;
  position: absolute;
}
.choice .choice__color .choice__color-radio {
  display: inline-flex;
  position: relative;
  margin: 0 6px 0 3px;
  cursor: pointer;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
@media (min-width: 720px) {
  .choice .choice__color .choice__color-radio {
    margin: 0 5px 0 3px;
  }
}
@media (min-width: 1360px) {
  .choice .choice__color .choice__color-radio {
    width: 26px;
    height: 26px;
  }
}
.choice .choice__color .choice__color-input:checked + .choice__color-radio::before {
  content: "";
  position: absolute;
  display: block;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  top: -4px;
  left: -4px;
  border: 1px solid;
}
@media (min-width: 1360px) {
  .choice .choice__color .choice__color-input:checked + .choice__color-radio::before {
    width: 32px;
    height: 32px;
    top: -3px;
    left: -3px;
  }
}
.choice .choice__color .choice__color-input:checked + .choice__color-radio--violet::before {
  border-color: #192B3F;
}
.choice .choice__color .choice__color-input:checked + .choice__color-radio--blueshgray {
  border-color: #8A94A6;
}
.choice .choice__color .choice__color-input:checked + .choice__color-radio--gray::before {
  border-color: #3D5876;
}
.choice .choice__color .choice__color-radio--violet {
  background-color: #192B3F;
}
.choice .choice__color .choice__color-radio--blueshgray {
  background-color: #8A94A6;
}
.choice .choice__color .choice__color-radio--gray {
  background-color: #3D5876;
}
.choice .choice__size {
  width: 100%;
  grid-column: span 2;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-left: -40px;
}
@media (min-width: 720px) {
  .choice .choice__size {
    margin-left: -25px;
    grid-column: span 1;
  }
}
.choice .choice__size .choice__size-text {
  width: 100%;
  padding: 25px 0 10px 0;
  margin-left: 40px;
  grid-column: span 4;
  flex-grow: 1;
}
@media (min-width: 720px) {
  .choice .choice__size .choice__size-text {
    padding: 2px 0 0 0;
    margin-left: 25px;
  }
}
@media (min-width: 1360px) {
  .choice .choice__size .choice__size-text {
    padding: 0 0 7px 0;
  }
}
.choice .choice__size .choice__size-input {
  appearance: none;
  position: absolute;
}
.choice .choice__size .choice__size-radio {
  position: relative;
  display: flex;
  padding-right: 20px;
  width: 48px;
  height: 48px;
  justify-content: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  font-family: "Rajdhani-Medium";
  color: #8A94A6;
  cursor: pointer;
}
@media (min-width: 720px) {
  .choice .choice__size .choice__size-radio {
    margin-right: 5px;
    padding: 8px 0 0 0;
  }
}
@media (min-width: 1360px) {
  .choice .choice__size .choice__size-radio {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    font-family: "Rajdhani-Medium";
    width: 32px;
    height: 32px;
    padding: 5px 0 0 5px;
  }
}
.choice .choice__size .choice__size-input:checked + .choice__size-radio {
  font-weight: 500;
  font-size: 32px;
  line-height: 24px;
  font-family: "Rajdhani-Medium";
  color: #192B3F;
}
@media (min-width: 1360px) {
  .choice .choice__size .choice__size-input:checked + .choice__size-radio {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    font-family: "Rajdhani-Bold";
    width: 32px;
    height: 32px;
  }
}
.choice .choice__size .choice__size-input:hover + .choice__size-radio::before {
  content: "";
  position: absolute;
  display: block;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 1px solid #8A94A6;
  transform: translate(0, -25%);
}
@media (min-width: 1360px) {
  .choice .choice__size .choice__size-input:hover + .choice__size-radio::before {
    width: 32px;
    height: 32px;
    transform: translate(0, -15%);
  }
}
.choice .choice__size .choice__size-input:checked + .choice__size-radio::before {
  content: "";
  position: absolute;
  display: block;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 1px solid #192B3F;
  transform: translate(0, -25%);
}
@media (min-width: 1360px) {
  .choice .choice__size .choice__size-input:checked + .choice__size-radio::before {
    width: 32px;
    height: 32px;
    transform: translate(0, -15%);
  }
}
.choice .guide {
  width: 100%;
  grid-column: span 2;
  padding: 15px 0 32px 0;
}
@media (min-width: 720px) {
  .choice .guide {
    grid-column: span 1;
    padding: 35px 0 0 25px;
  }
}
@media (min-width: 1360px) {
  .choice .guide {
    padding: 37px 0 0 0;
  }
}
.choice .guide .guide__link {
  width: 92px;
  height: 28px;
  padding: 2px 13px 2px 12px;
  border: 1px solid #C9B8A8;
  border-radius: 24px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  font-family: "Rajdhani-Medium";
  color: #C9B8A8;
}
@media (min-width: 720px) {
  .choice .choice__quantity {
    grid-column: span 1;
    margin-top: -5px;
  }
}
.choice .choice__cost {
  padding: 2px 0 0 10px;
}
@media (min-width: 720px) {
  .choice .choice__cost {
    grid-column: span 1;
    padding: 0;
  }
}

.copyright {
  display: inline-block;
  margin-top: 5px;
  font-weight: 500;
  font-size: 40px;
  line-height: 40px;
  font-family: "Rajdhani-Medium";
  color: #C9B8A8;
}
@media (min-width: 1360px) {
  .copyright {
    margin: 5px 2px 0 0;
  }
}

.copyright::before {
  content: "$";
}

.cent {
  font-weight: 500;
  font-size: 28px;
  line-height: 40px;
  font-family: "Rajdhani-Medium";
}

.details {
  padding-bottom: 38px;
}
@media (min-width: 1360px) {
  .details {
    padding-bottom: 48px;
  }
}
.details::before {
  content: "";
  position: absolute;
  display: block;
  width: 288px;
  height: 1px;
  background-color: #E1E6ED;
}
@media (min-width: 720px) {
  .details::before {
    width: 688px;
  }
}
@media (min-width: 1360px) {
  .details::before {
    width: 616px;
  }
}
.details .details__title {
  padding: 28px 0 8px 0;
}
@media (min-width: 720px) {
  .details .details__title {
    padding: 18px 0 8px 0;
  }
}
@media (min-width: 1360px) {
  .details .details__title {
    padding: 25px 0 8px 0;
  }
}
.details .details__subtitle {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  font-family: "Rajdhani-Medium";
  opacity: 0.8;
}
@media (min-width: 720px) {
  .details .details__subtitle {
    padding-right: 20px;
  }
}
@media (min-width: 1360px) {
  .details .details__subtitle {
    padding: 0 10px 8px 0;
  }
}
.details .details__subtitle-comment {
  display: block;
  padding-top: 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  font-family: "Rajdhani-Medium";
}
@media (min-width: 1360px) {
  .details .details__subtitle-comment {
    padding: 0;
  }
}

.visually-hidden {
  position: absolute;
  visibility: hidden;
}

.flex-wrapper {
  margin: 0 auto;
  width: 320px;
  display: flex;
  justify-content: center;
  position: relative;
}
@media (min-width: 720px) {
  .flex-wrapper {
    width: 720px;
  }
}
@media (min-width: 1360px) {
  .flex-wrapper {
    width: 600px;
    margin: 0;
  }
}

.slider-wrapper {
  max-width: 320px;
  width: 100%;
  height: 600px;
  position: relative;
}
@media (min-width: 720px) {
  .slider-wrapper {
    max-width: 720px;
  }
}
@media (min-width: 1360px) {
  .slider-wrapper {
    max-width: 600px;
    height: 768px;
    position: sticky;
    top: 0;
    left: 0;
  }
}

.btn-prev,
.btn-next {
  position: absolute;
  left: 25px;
  bottom: 19px;
  height: 32px;
  width: 32px;
  background-image: url(../img/arrow.svg);
  background-repeat: no-repeat;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
@media (min-width: 720px) {
  .btn-prev,
.btn-next {
    left: 25px;
    bottom: 18px;
  }
}
@media (min-width: 1360px) {
  .btn-prev,
.btn-next {
    left: 60px;
    bottom: 25px;
  }
}

.btn-next {
  transform: scale(-1, 1);
  left: 55px;
  right: auto;
}
@media (min-width: 720px) {
  .btn-next {
    left: 55px;
  }
}
@media (min-width: 1360px) {
  .btn-next {
    left: 90px;
    bottom: 25px;
  }
}

.btn-prev:hover,
.btn-next:hover {
  width: 40px;
  height: 40px;
  background-image: url(../img/arrow-hover.svg);
  transform: translate(-35%, -10%);
}

.btn-next:hover {
  transform: scale(-1, 1) translate(-20%, -10%);
}

.btn-prev:active,
.btn-next:active {
  background-image: url(../img/arrow-active.svg);
}

.slide {
  width: 100%;
  height: 100%;
  display: none;
}

.slide.active {
  display: block;
}

.slide__foto img {
  width: 320px;
  height: 600px;
  object-fit: cover;
  object-position: top;
}
@media (min-width: 720px) {
  .slide__foto img {
    width: 720px;
    object-position: 55px -20px;
    object-fit: none;
    background: linear-gradient(to top right, #D0C2B7, #C4AE9C);
  }
}
@media (min-width: 1360px) {
  .slide__foto img {
    width: 600px;
    height: 768px;
    object-position: center;
    object-fit: contain;
  }
}

@media (min-width: 720px) {
  .slide__foto--tablet img {
    width: 720px;
    object-position: bottom;
  }
}
@media (min-width: 1360px) {
  .slide__foto--tablet img {
    width: 600px;
    object-position: center;
  }
}

@media (min-width: 720px) {
  .slide__foto--pink img {
    background: linear-gradient(to top, #DD9179, #E9947D);
  }
}

.slide__numbertext {
  position: absolute;
  color: #ffffff;
  left: 176px;
  bottom: 27px;
  font-weight: 500;
  font-size: 20px;
  line-height: 32px;
  font-family: "Rajdhani-Medium";
}
@media (min-width: 1360px) {
  .slide__numbertext {
    left: 212px;
    bottom: 32px;
  }
}

.slide__numbertext-opacity {
  opacity: 0.5;
}

.slide__text {
  position: absolute;
  left: 16px;
  bottom: 91px;
  color: #ffffff;
}
@media (min-width: 720px) {
  .slide__text {
    bottom: 83px;
  }
}
@media (min-width: 1360px) {
  .slide__text {
    left: 52px;
    bottom: 96px;
  }
}

.slide__text h2 {
  font-weight: 400;
  font-size: 48px;
  line-height: 48px;
  font-family: "Rajdhani-Regular";
}
@media (min-width: 720px) {
  .slide__text h2 {
    font-weight: 400;
    font-size: 64px;
    line-height: 64px;
    font-family: "Rajdhani-Regular";
  }
}
@media (min-width: 1360px) {
  .slide__text h2 {
    font-weight: 400;
    font-size: 72px;
    line-height: 72px;
    font-family: "Rajdhani-Regular";
  }
}

.slide__text p {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  font-family: "Rajdhani-Medium";
  opacity: 0.8;
}
@media (min-width: 720px) {
  .slide__text p {
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    font-family: "Rajdhani-Medium";
  }
}
@media (min-width: 1360px) {
  .slide__text p {
    font-weight: 500;
    font-size: 28px;
    line-height: 32px;
    font-family: "Rajdhani-Medium";
  }
}

.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 6s;
  animation-name: fade;
  animation-duration: 6s;
}

@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
.slider-wrapper__logo {
  width: 65px;
  height: 36px;
  position: absolute;
  top: 0;
  left: 0;
  margin: 24px 0 0 16px;
  z-index: 1;
}
@media (min-width: 1360px) {
  .slider-wrapper__logo {
    width: 95px;
    height: 53px;
    margin: 32px 0 0 52px;
  }
}

.counter {
  width: 78px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.counter .counter__btn {
  width: 16px;
  height: 16px;
  border: none;
  display: block;
  background-color: #ffffff;
  position: relative;
}
.counter .counter__btn:hover:before {
  content: "";
  position: absolute;
  display: block;
  width: 28px;
  height: 28px;
  border: 1px solid #E1E6ED;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.counter .counter__input {
  appearance: none;
  opacity: 0;
  cursor: pointer;
}
.counter .minus {
  background-image: url(../img/minus.svg);
}
.counter .counter__value {
  width: 46px;
  display: flex;
  text-align: center;
  font-weight: 500;
  font-size: 24px;
  line-height: 24px;
  font-family: "Rajdhani-Regular";
  color: #8A94A6;
  border: none;
}
.counter .plus {
  background-image: url(../img/plus.svg);
}

